.notification {
    background: green;
    font-size: 20px;
    border-style: solid;
    padding: 10px;
    margin-bottom: 10px;
    -webkit-animation-name: fade;
            animation-name: fade;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-delay: 3s;
            animation-delay: 3s;
  }

  @-webkit-keyframes fade {
      from {opacity: 1;}
      to {opacity: 0;}
  }

  @keyframes fade {
      from {opacity: 1;}
      to {opacity: 0;}
  }


